import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { TableViewModel } from "../../Model/ViewModel/TableViewModel";

/**
 * Generated class for the TableModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-table-add-edit-modal",
  templateUrl: "table-add-edit-modal.html"
})
export class TableAddEditModalPage {
  item: TableViewModel;
  mode: string;
  title:string;
  CanEditTable:boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.item = new TableViewModel();
    this.item.schemaname=navParams.data.item.schemaname;
    this.item.tablename=navParams.data.item.tablename;
    this.item.tablecname=navParams.data.item.tablecname;
    this.item.file_group=navParams.data.item.file_group;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.table_cat_key=navParams.data.item.table_cat_key;
    this.item.table_cat_desc=navParams.data.item.table_cat_desc;
    this.item.err_columnname=navParams.data.item.err_columnname;
    this.mode = navParams.data.mode;
    this.CanEditTable=navParams.data.CanEditTable;
    if(this.mode==="POST")
     this.title="新增";
   else
     this.title="更新";
  }
  SelectCat() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.table_cat_key,
      code_type:"063",
      code_key:"",
      title:"類別",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.table_cat_key = select_data.code_key;
      this.item.table_cat_desc=select_data.code_desc;
    });

    modal.present();
  }
  SelectErrColumn() {
    let modal = this.modalCtrl.create("TableDetailSelectModalPage", {
      table_item: this.item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.err_columnname = select_data.columnname;
    });

    modal.present();
  }
  SelectTable() {
    let modal = this.modalCtrl.create("TableSelectModalPage", {
      select_item: this.item.tablefullname,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.schemaname = select_data.schemaname;
       this.item.tablename=select_data.tablename;
       this.item.tablefullname=select_data.tablefullname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad TableModalPage");
  }
}



